* {
  font-family: "Avenir";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
}
h1 {
  margin: 10px;
  text-align: center;
  font-size: 30px;
  text-transform: capitalize;
}
h4,
h5 {
  text-align: center;
  margin: 5px auto 20px auto;
}
hr {
  border-radius: 0;
  width: 75%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: block;
  margin: 5px auto 20px auto;
}
.add-button-container {
  margin-bottom: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.add-wishlist-button {
  border: none;
  background-color: #fff;
  width: 100%;
  height: 40px;
  font-size: 14px;
  color: #000;
  outline: none;
  text-indent: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.25s ease-in-out;
  border: 1px solid #000;
  border-right: none;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.add-wishlist-button:hover {
  transition: 0.25s ease-in-out;
  opacity: 1;
}
.add-wishlist-input {
  border: none;
  background-color: #fff;
  width: 300px;
  height: 40px;
  margin: 10px;
  font-size: 14px;
  border-radius: 7px;
  cursor: pointer;
  transition: 0.25s ease-in-out;
  opacity: 0.9;
  text-indent: 10px;
  outline: none;
}
.add-wishlist-input::placeholder {
  color: black;
  text-indent: 10px;
}
.new-wishlist-container {
  border-radius: 5px;
  border: none;
  width: 30%;
  padding: 10px;
  display: block;
  margin: 100px auto 10px auto;
  box-sizing: border-box;
}
.row-title {
  width: 100%;
  color: #000;
  text-transform: capitalize;
  border-radius: 5px;
}
.back-button {
  color: #000;
  text-decoration: none;
  background-color: #80ffaa;
  padding: 5px 10px;
  border-radius: 5px;
}
@media screen and (max-width: 1200px) {
  .new-wishlist-container {
    width: 100%;
  }
}
@media screen and (max-width: 300px) {
  table {
    width: 100%;
  }
}
