table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 75%;
  margin: 0 auto;
}

th {
  font-weight: bold;
  background-color: #80ffaa;
  border-radius: 5px;
  text-align: left;
  border: 2px solid #fff;
}

td,
th {
  padding: 10px;
  white-space: normal;
  word-wrap: break-word;
  max-width: 200px;
}
tr:nth-child(odd) td {
  background-color: #f2f2f2;
  border: 2px solid #fff;
}
@media screen and (max-width: 1200px) {
  table {
    width: 90%;
  }
}
